import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import moment from 'moment';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {


  duration: any
  targetTime: any
  currentTime: any

  days: string = '00'
  hours: string = '00'
  minutes: string = '00'
  seconds: string = '00'

  
  constructor(public navCtrl: NavController) { }

  ionViewDidEnter() {
  }

  start() {
    this.targetTime = moment(moment()).add(2,'days');
    setInterval(function(){
      this.currentTime = moment();
      var diffTime = this.targetTime - this.currentTime ;
      this.duration = moment.duration(diffTime, 'milliseconds');
      this.updateDisplays()
    }, 1000);
  }

  Stop() {
    
  }

  reset() {

  }

  updateDisplays() {
    this.days = this.duration.days()
    this.hours = this.duration.hours()
    this.minutes = this.duration.minutes()
    this.seconds = this.duration.seconds()
    console.log(this.days + ":" + this.hours + ":" + this.minutes + ":" + this.seconds)
  }

}



